  switchUpdateUi(value) {
    this.errorMessage = '';
    switch (value) {
      case 'Password': {
        this.isChangePasswordClicked = UserSession.switch(this.isChangePasswordClicked);
        if (this.isChangePasswordClicked) {
          this.redirectToHome = false;
          this.isChangePhoneClicked = false;
          this.isChangeEmailClicked = false;


        }
        this.resetCahngePasswordForm();

        break;
      }
      case 'Phone': {
        this.isChangePhoneClicked = UserSession.switch(this.isChangePhoneClicked);
        if (this.isChangePhoneClicked) {
          this.redirectToHome = false;
          this.isChangePasswordClicked = false;
          this.isChangeEmailClicked = false;

        }
        break;
      }
      case 'Email': {
        this.isChangeEmailClicked = UserSession.switch(this.isChangeEmailClicked);
        if (this.isChangeEmailClicked) {
          this.redirectToHome = false;
          this.isChangePasswordClicked = false;
          this.isChangePhoneClicked = false;
          this.isEmailAvailable = false;
          this.isEmailAlreadyExist = false;
        }
        break;
      }
    }

  }
  changePassword() {
    this.markFormGroupTouched(this.changepasswordForm);
    if (!this.changepasswordForm.valid && !this.changepasswordForm.dirty) {
      this.errorMessage = Constants.fieldRequiredMessage;
    }
    if (this.changepasswordForm.value.oldpassword === this.changepasswordForm.value.password) {
        this.errorMessage = 'Old password and new password is same';
        this.showAlert(33);
        return;
    }
    if (this.changepasswordForm.valid) {
      this.http_user.changeUserPassword(LocalStorageService.getUserId(), this.changepasswordForm.value.oldpassword, this.changepasswordForm.value.password, this.changepasswordForm.value.confirmPassword, this, true);

    }
  }

  changePhone() {

    if (this.updatephoneForm.valid) {
      this.http_user.changePhonenumber(LocalStorageService.getUserId(), this.updatephoneForm.value.phone, this, true)
    } else {
      this.markFormGroupTouched(this.updatephoneForm);
    }
  }
  changeEmail() {
    if (this.updateemailForm.valid) {
      this.isShowSmallLoader = true;
      this.common_http.validate('email', this.updateemailForm.value.email, this, false);

      // this.http_user.changeEmail(LocalStorageService.getUserId(),this.updateemailForm.value.email,this,true);

    } else {
      this.markFormGroupTouched(this.updateemailForm);
    }

  }
