import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { EditProfileComponent } from './editprofile.component';
import { CommonComponentsModule } from '../../common/common.module';
import { Ng2CompleterModule } from '../../core/autocomplete/ng2-completer.module';
import { routing } from './editProfile.routing';
import { PlaceAutoCompleteModule } from "common/placeautocomplete/placeautocomplete.module";
import { ImageUploadModule } from 'ng2-imageupload';
import { ImageCropModule } from 'imageCroppingPopup/imageCrop.module';
@NgModule({
  imports: [
    ReactiveFormsModule,
    ImageUploadModule,
    FormsModule,
    CommonModule,
    CommonComponentsModule,
    PlaceAutoCompleteModule,
    Ng2CompleterModule,
    routing,
    ImageCropModule
  ],
  declarations: [
    EditProfileComponent
  ]
})
export class EditUserProfileModule { }



